.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}


/* Strip default margin/padding/font-weight  */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
button {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
/* Strip ul default styling  */
ul {
  list-style: none;
}
/* Strip anchor tag default styling  */
a {
  text-decoration: none;
  color: inherit;
}
/* Strip button default styling (and add pointer cursor)  */
button {
	font-family: inherit;
	color: inherit;
  border: none;
  background: none;
  cursor: pointer;
}
/* ensure images fit inside container  */
img {
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}
/* strip default address styling  */
address {
  font-style: normal;
}
/* set root font size to 10px for simple rem conversion  */
html {
  font-size: 62.5%;
}
body {
	font-size: 1.6rem;
	overflow-x: hidden;
}
select {
	font-family: inherit;
}


.wrapper {
	margin: 0 auto;
	width: 90%;
	max-width: 1400px;
}

body {
	font-family: 'Arimo', sans-serif;
}

h1, h2, h3 {
	letter-spacing: 0.025em;
}

.App {
	min-height: 100vh;
	position: relative;
	padding-bottom: 6rem;
}

main {
	padding: 4rem 0;
}

.main-container {
	display: flex;
	justify-content: space-between;

	width: 100%;
}

.main-top {
	display: flex;
}

.main-left {
	padding-right: 3rem;
}

h2 {
	font-weight: bold
}
@media (max-width: 1280px) {
	.main-container {
		flex-direction: column;
	}

	.main-left {
		padding-right: 0;
	}

	.main-right {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-evenly;
		align-items: start;
	}

	.main-right .MapComponent {
		padding-top: 0;
		width: 40vw;
	}

	.main-right .leaflet-container {
		width: 40vw;
	}
}
@media (max-width: 768px) {
	
	header .header-container {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		flex-wrap: nowrap;

		height: 20rem;
	}

	header .header-right {
		position: static;
		height: 40%;

		flex-direction: column-reverse;
	}

	.header-right .Units {
		position: static;
	}

	header h1 {
		height: 45%;
	}

	main .forecast-week {
		grid-template-columns: repeat(4, 1fr);
	}

	main .forecast-hourly {
		grid-template-columns: repeat(7, 1fr);
	}

	.main-right {
		flex-direction: column;
	}

	.LocationList {
		width: 100%;
	}

	.main-right .MapComponent {
		width: 100%;
	}

	.main-right .leaflet-container {
		width: 100%;
	}

}

.searchBar input {
	font-size: clamp(1.6rem, 0.8rem + 0.8vw, 2rem);

	padding: 0.5rem 1.5rem;

	border: none;
	border-radius: 1rem;

	height: 3.5rem;

}

.searchBar input:focus {
	box-shadow: 0 0 5px #FFFFFF;
}

.searchBar button {
	font-size: 2rem;

	margin-left: 0.5rem;

	background-color: #FFFFFF;
	border-radius: 1rem;

	width: 3.5rem;
	height: 3.5rem;
}

.searchBar button:hover,
.searchBar button:focus {
	background-color: #3d7ac0;
	border-radius: 1rem;

	color: white;
}

.searchBar button:active {
	color: black;
}
.Units {
	display: flex;
	align-items: center;

	position: absolute;
	top: 2rem;
	right: 0;
}

.Units .selected {
	font-weight: bold;
}

.Units button {
	padding: 0.5rem 0.4rem;
}

.Units button:hover,
.Units button:focus {
	text-decoration: underline;
}

.Units button:active {
	font-weight: bold;
}
.weather-icon {
	max-width: 10rem;
	max-height: 10rem;
}
header {
	background-color: #b7d6f3;
}

.header-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	height: 16rem;
}

.header-right {
	display: flex;
	align-items: center;

	position: relative;

	height: 100%;
}

h1 {
	font-size: 4rem;
	font-weight: bold;

}

h1 span {
	font-weight: normal;
}

h1 a {
	display: flex;
	align-items: center;
}

.searchBar {
	padding-left: 2rem;
}
.currentWeather-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.currentWeather-container h2 {
	padding-bottom: 0.2rem;	
}

.currentWeather {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;

	align-items: center;

	padding: 1rem 0 3rem;
}

.currentWeather p {
	font-size: 1.8rem;
	padding: 0.2rem 0;
}

.currentWeather .currentWeather-temp {
	font-size: 2.8rem;
}

.currentWeather-right {
	display: flex;
	flex-direction: column;

	padding-left: 1rem;
}

.currentWeather-middle {
	display: flex;
	flex-direction: column;

	justify-content: center;
	align-items: center;
}

.currentWeather .description {
	text-transform: capitalize;
	font-size: 1.6rem;
}
.forecast-week-container {
	display: flex;
	flex-direction: column;
	align-items: center;

	padding-top: 1rem;
}

.forecast-week {
	display: grid;
	grid-template-columns: repeat(7, 1fr);
	grid-template-rows: 1fr;

	padding: 1rem 0;
}

.forecast-day {
	text-align: center;

	padding: 1rem;
}

.forecast-date {
	font-size: 1.8rem;
}

.forecast-day .description {
	font-size: 1.6rem;
	text-transform: capitalize;
	padding-bottom: 0.5rem;
}
.forecast-hourly-container {
	display: flex;
	flex-direction: column;
	align-items: center;

	padding: 4rem 0;
	margin: 0 auto;
}

.forecast-hourly {
	display: grid;
	grid-template-columns: repeat(14, minmax(5.5rem, 1fr));
	grid-template-rows: 1fr;

	padding-top: 1rem;
}

.forecast-hour {
	padding: 0.5rem;
	text-align: center;
}

.forecast-hourly button {
	padding: 1rem;
}

.forecast-hourly button.disabled {
	opacity: 20%;
}

.forecast-hourly button:not(.disabled):hover,
.forecast-hourly button:not(.disabled):focus {
	background-color: #5f92cc;
	color: white;
}

.forecast-hourly button:not(.disabled):active {
	color: black;
}
.LocationList {
	border: 3px solid #b7d6f3;
	border-radius: 2rem;

	margin-bottom: 2rem;

	overflow: hidden;
}

.LocationList-remove {
	padding: 0.5rem 0.5rem 0.5rem 1rem;
}

.LocationList-remove:hover,
.LocationList-remove:focus {
	background-color: #e93232;
	color: white;
}

.LocationList-remove:active {
	color: black;
}

.LocationList h3 {
	font-weight: bold;
	letter-spacing: 0.05em;
	text-align: center;

	padding: 1.5rem;
}

.LocationList-loc {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.LocationList p {
	text-align: center;
	padding: 1rem;
}

.LocationList-loc button {
	padding: 1.5rem 2rem;
}

.LocationList-select {
	text-align: left;
	width: 100%;
}

.LocationList-select:hover,
.LocationList-select:focus {
	background-color: #5f92cc;
	color: white;
}

.LocationList-select:active {
	color: black;
}

.LocationList li {
	width: 100%;
}

.LocationList .selected {
	background-color: #e9f3ff;
}

	.MapComponent {
		padding-top: 2rem;
	}
	
	.leaflet-container {
			width: calc(10rem + 20vw);
			height: 30rem;
			background-color:transparent;
	}

	.mapLayer {
		display: flex;
		justify-content: space-evenly;
		flex-wrap: wrap;
	}

	.mapLayer label {
		padding-left: 1rem;
	}

	.mapLayer-control {
		padding: 0.3rem 0;
	}

	.mapLayer input {
		position: absolute;
		opacity: 0;
	}

	.mapLayer label {
		text-align: center;

		display: inline-block;
		width: 11.5rem;

		cursor: pointer;

		padding: 0.5rem 1rem;

		background-color: #e9f3ff;
		border: 2px solid #e9f3ff;
		border-radius: 1.5rem;
	}

	.mapLayer input:checked + label {
		border: 2px solid #5f92cc;
	}

	.mapLayer label:hover,
	.mapLayer input:focus + label {
		background-color: #5f92cc;
		color: white;
	}

	.mapLayer input:active + label {
		color: black;
	}
footer {
	background-color: #4b545d;
	position: absolute;
	bottom: 0;
	width: 100%;

}

.footer-container {
	padding: 2.4rem 0;
	color: white;

	display: flex;
	justify-content: space-between;
}

footer a {
	text-decoration: underline;
}

footer a:hover,
footer a:focus {
	opacity: 50%;
}
