.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}


/* Strip default margin/padding/font-weight  */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
button {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
/* Strip ul default styling  */
ul {
  list-style: none;
}
/* Strip anchor tag default styling  */
a {
  text-decoration: none;
  color: inherit;
}
/* Strip button default styling (and add pointer cursor)  */
button {
	font-family: inherit;
	color: inherit;
  border: none;
  background: none;
  cursor: pointer;
}
/* ensure images fit inside container  */
img {
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}
/* strip default address styling  */
address {
  font-style: normal;
}
/* set root font size to 10px for simple rem conversion  */
html {
  font-size: 62.5%;
}
body {
	font-size: 1.6rem;
	overflow-x: hidden;
}
select {
	font-family: inherit;
}


.wrapper {
	margin: 0 auto;
	width: 90%;
	max-width: 1400px;
}

body {
	font-family: 'Arimo', sans-serif;
}

h1, h2, h3 {
	letter-spacing: 0.025em;
}